import Score from '../Score'

export default class Round {
  scores = []

  constructor(index, options = {}) {
    this.index = index
    this.options = options
    if (!this.options.scoresPerRound) {
      // ASSUMPTION: always 3 scores per round
      // In any real world dart games, always 3 darts for 3 scores per round.
      // But we can do different configuration for other needs in the future.
      this.options.scoresPerRound = 3
    }
  }

  addScore = score => {
    try {
      if (this.isNewScoreAddable()) {
        let scoreObject
        if (score instanceof Score) {
          scoreObject = score
          scoreObject.index = this.scores.length
        } else {
          scoreObject = new Score(score, this.scores.length, this.options)
        }
        if (scoreObject.valid) {
          this.scores.push(scoreObject)
        } else {
          throw new Error('The `score` is invalid!')
        }
        return scoreObject
      } else {
        throw new Error('The `scores` array cannot contain more than 3 Score objects!')
      }
    } catch(e) {
      console.error(e)
      return null
    }
  }

  getCurrentScore = () => {
    return this.scores[this.scores.length - 1]
  }

  isNewScoreAddable = () => {
    return this.scores.length < this.options.scoresPerRound && !this.scores.some(s => s.symbol === 'BUST')
  }
}
